import React, { useEffect, useState, useContext }  from 'react';
import { useHistory } from "react-router-dom";
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { useTranslation } from 'react-i18next';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import GridList from '@material-ui/core/GridList';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import { MedContext } from '../internalMedContext';
import UserContext from '../context/UserContext';
import Axios from "axios";

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    gridList: {
        width: 1000,
       
        textAlign: 'center',
        margin:10,
        padding:3
      },
    button: {
        marginRight: theme.spacing(1),
    },
    instructions: {
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: 'black',
    },
}));




const CenteredGrid = () => {
    const { t, i18n } = useTranslation();
    const problemtodays = [
        {name: t('internalMedcine.Fever') + "/", value:"Fever"},
        {name: t('internalMedcine.Cough')+"/", value:"Cough"},
        {name: t('internalMedcine.Runnynose') +"/", value:"Runnynose"},
        {name: t('internalMedcine.Phlegm') +"/", value:"Phlegm"},
        {name: t('internalMedcine.Difficultybreathing')+"/", value:"Difficultybreathing"},
        {name: t('internalMedcine.Palpitation')+" /", value:"Palpitation"},
        {name: t('internalMedcine.Feelsulggish')+" /", value:"Feelsulggish"},
        {name: t('internalMedcine.Geteasilytired')+"/", value:"Geteasilytired"},
        {name: t('internalMedcine.Shortnessof')+"/", value:"shortness of breath"},
        {name: t('internalMedcine.Dizziness')+" /", value:"Dizziness"},
        {name: t('internalMedcine.Lossappetite')+" /", value:"Lossappetite"},
        {name: t('internalMedcine.Vomiting')+"/", value:"Vomiting"},
        {name: t('internalMedcine.Bloodystool')+"/", value:"Bloodystool"},
        {name: t('internalMedcine.Frequent')+"/", value:"frequenturination"},
        {name: t('internalMedcine.Bloodyurine')+"/", value:"Bloodyurine"},
        {name: t('internalMedcine.Weightloss')+"/", value:"Weightloss"},
        {name: t('internalMedcine.Feelthirsty')+"/", value:"Feelthirsty"},
        {name: t('internalMedcine.Hypertension')+"/", value:"Hypertension"},
        {name: t('internalMedcine.Paralysis' )+"/ ", value:"Paralysis"},
        {name: t('internalMedcine.Swelling')+"/", value:"Swelling"},
        {name: t('internalMedcine.Hives')+"/", value:"Hives"},
        {name: t('internalMedcine.Insomnia' ), value:"Insomnia"},
        {name: t('internalMedcine.Numbness')+"/", value:"Numbness"},
        {name: t('internalMedcine.Nausea')+"/", value:"Nausea"},
        {name: t('internalMedcine.Diarrhea')+"/", value:"Diarrhea"},
        {name: t('internalMedcine.Itchiness')+"/", value:"itchiness"},
        {name: t('internalMedcine.Pain')+"/", value:"Pain"},
        {name: t('internalMedcine.Other') +"/", value:"others"},
        
       ]

       const stools = [
        {name: t('internalMedcine.Grayishwhite') +"/ ", value:"Grayishwhite"},
        {name: t('internalMedcine.Brown') +"/ ", value:"Brown"},
        {name: t('internalMedcine.Black') +"/", value:"Black"},
        {name: t('internalMedcine.bloody') +"/", value:"bloody"},
        {name: t('internalMedcine.watery') +"/", value:"watery"},
        {name: t('internalMedcine.Soft') +"/", value:"Soft"},
        {name: t('internalMedcine.Hard') +"/", value:"Hard"},
       ]
    

    const [values, setValues] = useContext(MedContext);
    const classes = useStyles();
    const [checkedProblemtoday, setCheckedProblemtoday] = React.useState([]);
    const [checkedStool, setCheckedStool] = React.useState([]);
    const [frequency, setFrequency] = React.useState('');
    const { userData } = useContext(UserContext);
    const history = useHistory();

    useEffect(() => {
        if (!userData.user) history.push("/login1");
    });

    const authAxios = Axios.create({
        baseURL: "http://localhost:5000",
        headers: {
          'x-auth-token': userData.token,
        },
      });

    const logFunction = async (question) =>{
        const loginfo = {
            inteviewName: "Internal Medcine form 2",
            //userName: userData.user.userName,
            language: i18n.language,
            contentSentence: question,
            date: new Date,
            userId: userData.user.id,
          };
          const loginInput = await authAxios.post(
            "/logfile/insert",
            loginfo
          );
    }  

    const handleToggleProblemtoday = (object) => () => {
        const currentIndex = values.problemtoday.indexOf(object.value);
        const newChecked = [...values.problemtoday];
        
        if (currentIndex === -1) {
            newChecked.push(object.value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setCheckedProblemtoday(newChecked);
        setValues({...values, problemtoday:newChecked});
        logFunction("What is the problem today");
    };

    const handleToggleStool = (object) => () => {
        const currentIndex = values.stools.indexOf(object.value);
        const newChecked = [...values.stools];

        if (currentIndex === -1) {
            newChecked.push(object.value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setCheckedStool(newChecked);
        setValues({...values, stools:newChecked});
        logFunction("How is your stool like");
    };

    const updateFrequency = (event) => {
        setFrequency(event.target.value);
        setValues({...values, stoolfrequency:event.target.value});
        logFunction("How is your stool frequency");
    };

    return (

        <div>
            <div className={classes.root}>
                <Grid container spacing={3} style={{ padding: 20 }}>
                    <Grid item xs={12}>
                        <Paper className={classes.paper}><strong><h4>{t('internalMedcine.problemtoday')}<br/>/.)</h4></strong></Paper>
                    </Grid>
                    
                    <Grid container spacing={3} style={{ padding: 20 }}>
                    <GridList container style={{height:370, padding: 20 }} cellHeight={10} className={classes.gridList} cols={3}>
                        {problemtodays.map((value) => {
                            const labelId = `checkbox-list-label-${value.name}`;

                            return (
                                <ListItem key={value.name} role={undefined} dense button onClick={handleToggleProblemtoday(value)}>
                                    <ListItemIcon>
                                        <Checkbox
                                            edge="start"
                                            checked={values.problemtoday.indexOf(value.value) !== -1}
                                            tabIndex={-1}
                                            disableRipple
                                            inputProps={{ 'aria-labelledby': labelId }}
                                        />
                                    </ListItemIcon>
                                    <ListItemText id={labelId} primary={`${value.name}`} />

                                </ListItem>
                            );
                        })}
                    </GridList>

                    <Grid item xs={12}>
                        <Paper className={classes.paper}><strong><h4>Check all that apply about your stool. <br/>/ </h4></strong></Paper>
                    </Grid>
                        
                    <GridList container style={{height:107, padding: 20 }} cellHeight={10} className={classes.gridList} cols={3}>
                        {stools.map((value) => {
                            const labelId = `checkbox-list-label-${value.name}`;

                            return (
                                <ListItem key={value.name} role={undefined} dense button onClick={handleToggleStool(value)}>
                                    <ListItemIcon>
                                        <Checkbox
                                            edge="start"
                                            checked={values.stools.indexOf(value.value) !== -1}
                                            tabIndex={-1}
                                            disableRipple
                                            inputProps={{ 'aria-labelledby': labelId }}
                                        />
                                    </ListItemIcon>
                                    <ListItemText id={labelId} primary={`${value.name}`} />

                                </ListItem>
                            );
                        })}
                    </GridList>
                    <Grid item xs={6}>
                    <FormControl fullWidth xs={8} className={classes.formControl}>
                        <InputLabel id="demo-simple-select-label">{t('internalMedcine.Stoolfrequency')}/ </InputLabel>
                        <Select 
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={values.stoolfrequency}
                            onChange={updateFrequency}
                        >
                            <MenuItem value={1}>1</MenuItem>
                            <MenuItem value={2}>2</MenuItem>
                            <MenuItem value={3}>3</MenuItem>
                            <MenuItem value={4}>4</MenuItem>
                            <MenuItem value={5}>5</MenuItem>
                        </Select>
                        </FormControl>
                        </Grid>
                        </Grid>
                    </Grid>

            </div>
        </div>

    )
}
export default CenteredGrid;