import React, { useEffect, useState, useContext } from 'react'
import { useHistory } from "react-router-dom"
import { makeStyles } from '@material-ui/core/styles';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import { Grid } from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import { useTranslation } from 'react-i18next';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import GridList from '@material-ui/core/GridList';
import FormLabel from '@material-ui/core/FormLabel';
import { MedContext } from '../internalMedContext';
import UserContext from '../context/UserContext';
import Axios from "axios";

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    gridList: {
        width: 1000,

        textAlign: 'center',
        margin: 10,
        padding: 5
    },
    button: {
        marginRight: theme.spacing(1),
    },
    instructions: {
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: 'black',
    },
}));
const CenteredGrid = () => {
    const { t, i18n } = useTranslation();

    const hadsurgerys = [
        {name: t('internalMedcine.Eye') +"/ ", value:"Eye"},
        {name: t('internalMedcine.Ear') +"/ ", value:"Ear"},
        {name: t('internalMedcine.Nose') +"/ ", value:"Nose"},
        {name: t('internalMedcine.Mouth') +"/ ", value:"Mouth"},
        {name: t('internalMedcine.Throat') +"/ ", value:"Throat"},
        {name: t('internalMedcine.neck') +"/ ", value:"Neck"},
        {name: t('internalMedcine.Brest') +"/ ", value:"Brest"},
        {name: t('internalMedcine.Esophagus') +"/ ", value:"Esophagus"},
        {name: t('internalMedcine.Stomach') +"/ ", value:"Stomach"},
        {name: t('internalMedcine.Intestines') +"/ ", value:"Intestines"},
        {name: t('internalMedcine.Heart') +"/ <", value:"Heart"},
        {name: t('internalMedcine.Cecum') +"/ ", value:"Cecum"},
        {name: t('internalMedcine.Liver') +"/ ", value:"Liver"},
        {name: t('internalMedcine.Pancreas') +"/ ", value:"Pancreas"},
        {name: t('internalMedcine.kidney') +"/ ", value:"kidney"},
        {name: t('internalMedcine.Ovary') +"/ ", value:"Ovary"},
        {name: t('internalMedcine.Uterus') +"/ ", value:"Uterus"},
        {name: t('internalMedcine.Upperlowerlimb') +"/ ", value:"EUpperlowerlimbye"},
        {name: t('internalMedcine.Others') +"/ ", value:"Others"}
       ]

    const onmedications = [
        {name: t('internalMedcine.Coldmedicine') +"/ ", value:"Coldmedicine"},
        {name: t('internalMedcine.Feverreducer') +"/ ", value:"Feverreducer"},
        {name: t('internalMedcine.Painkiller') +"/ ", value:"Painkiller"},
        {name: t('internalMedcine.Anti-suppuration') +"/ ", value:"Anti-suppuration"},
        {name: t('internalMedcine.Antibiotics') +"/ ", value:"Antibiotics"}
       ]

       const ondoctorcares = [
        {name: t('internalMedcine.bronchialasthma') +"/  /", value:"bronchialasthma"},
        {name: t('internalMedcine.highbloodpressure') +"/ ", value:"highbloodpressure"},
        {name: t('internalMedcine.Diabetesmellitus') +"/ ", value:"Diabetesmellitus"},
        {name: t('internalMedcine.Tuberculosis') +"/ ", value:"Tuberculosis"},
        {name: t('internalMedcine.Hearttrouble') +"/ ", value:"Hearttrouble"},
        {name: t('internalMedcine.HepatitisB') +"/ ", value:"HepatitisB"},
        {name: t('internalMedcine.epatitisC') +"/ ", value:"epatitisC"},
        {name: t('internalMedcine.collagenConnectiveTissuedisease') +"/ ", value:"collagenConnectiveTissuedisease"},
        {name: t('internalMedcine.Thyroiddisease') +"/ ", value:"Thyroiddisease"},
        {name: t('internalMedcine.Cancertumor') +"/ ", value:"Cancertumor"},
        {name: t('internalMedcine.glaucoma') +"/ ", value:"glaucoma"},
        {name: t('internalMedcine.Gastrointestinaldisorder') +"/ ", value:"BlooGastrointestinaldisorderddisease"},
        {name: t('internalMedcine.Gonorrhea') +"/ ", value:"Gonorrhea"},
        {name: t('internalMedcine.Syphilis') +"/ ", value:"Syphilis"}
       ]

    const [values, setValues] = useContext(MedContext);
    const classes = useStyles();
    const [medication, setMedication] = React.useState('');
    const [medicationType, setMedicationType] = React.useState([]);
    const [doctorcares, setDoctorcares] = React.useState('');
    const [doctorcaresType, setDoctorcaresType] = React.useState([]);
    const [hadsurgery, setHadsurgerys] = React.useState('');
    const [hadsurgerysType, setHadsurgerysType] = React.useState([]);
    const { userData } = useContext(UserContext);
    const history = useHistory();

    useEffect(() => {
        if (!userData.user) history.push("/login1");
    });
    
    const authAxios = Axios.create({
        baseURL: "http://localhost:5000",
        headers: {
          'x-auth-token': userData.token,
        },
      });

    const logFunction = async (question) =>{
        const loginfo = {
            inteviewName: "Internal Medcine form 5",
            //userName: userData.user.userName,
            language: i18n.language,
            contentSentence: question,
            date: new Date,
            userId: userData.user.id,
          };
          const loginInput = await authAxios.post(
            "/logfile/insert",
            loginfo
          );
    } 

    const handleToggleHadsurgerysType = (object) => () => {
        const currentIndex = values.hadsurgerys.indexOf(object.value);
        const newChecked = [...values.hadsurgerys];

        if (currentIndex === -1) {
            newChecked.push(object.value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setHadsurgerysType(newChecked);
        setValues({...values, hadsurgerys:newChecked});
        logFunction("Have had surgery option");
    };

    const handleChangeHadsurgerys = (event) => {
        setHadsurgerys(event.target.value);
        setValues({...values, hadsurgery:event.target.value});
        logFunction("Have had surgery");
    };


    const handleToggleMedicationType = (object) => () => {
        const currentIndex = values.onmedications.indexOf(object.value);
        const newChecked = [...values.onmedications];

        if (currentIndex === -1) {
            newChecked.push(object.value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setMedicationType(newChecked);
        setValues({...values, onmedications:newChecked});
        logFunction("Are you curently on any medication option");
    };

    const handleChange = (event) => {
        setMedication(event.target.value);
        setValues({...values, onMedication:event.target.value})
        logFunction("Are you curently on any medication");
    };

    const handleToggleDoctorcaresType = (object) => () => {
        const currentIndex = values.doctorCare.indexOf(object.value);
        const newChecked = [...values.doctorCare];

        if (currentIndex === -1) {
            newChecked.push(object.value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setDoctorcaresType(newChecked);
        setValues({...values, doctorCare:newChecked});
        logFunction("Are you curently on doctor care option");
    };

    const handleChangeDoctorcare = (event) => {
        setDoctorcares(event.target.value);
        setValues({...values, doctorcare:event.target.value});
        logFunction("Are you curently on doctor care");
    };


    return (
        <div className={classes.root}>
            <Grid container spacing={3} style={{ padding: 20 }}>
                <Grid item xs={12}>
                    <Paper className={classes.paper}><strong><h5>{t('internalMedcine.currentlyonmedication')} <br />/</h5></strong></Paper>
                </Grid>

                <Grid item xs={12}>
                <FormControl component="fieldset">
                    
                    <RadioGroup row aria-label="gender" name="gender1" value={values.onMedication} onChange={handleChange}>
                        <FormControlLabel value="Yes" control={<Radio />} label="Yes" />
                        <FormControlLabel value="No" control={<Radio />} label="No" />
                    </RadioGroup>
                </FormControl>
                </Grid>

                <Grid>
                <div>
                    {values.onMedication === 'Yes' ? (
                        <div>
                        <FormLabel style={{marginLeft:-148, fontSize:18}}><strong>{t('internalMedcine.currentlyonmedicationyesno')}</strong></FormLabel>
                        <GridList style={{ height: 120, padding: 20 }} cellHeight={10} className={classes.gridList} cols={3}>
                        {onmedications.map((value) => {
                            const labelId = `checkbox-list-label-${value.name}`;

                            return (
                                <ListItem key={value.name} role={undefined} dense button onClick={handleToggleMedicationType(value)}>
                                    <ListItemIcon>
                                        <Checkbox
                                            edge="start"
                                            checked={values.onmedications.indexOf(value.value) !== -1}
                                            tabIndex={-1}
                                            disableRipple
                                            inputProps={{ 'aria-labelledby': labelId }}
                                        />
                                    </ListItemIcon>
                                    <ListItemText id={labelId} primary={`${value.name}`} />

                                </ListItem>
                            );
                        })}
                        </GridList>
                        </div>
                    ) : (
                            <nav> &apos; </nav>
                        )}
                </div>
                </Grid>

                <Grid item xs={12}>
                    <Paper className={classes.paper}><strong><h5>{t('internalMedcine.underdoctorcare')}? <br />/</h5></strong></Paper>
                </Grid>
                <Grid item xs={12}>
                <FormControl component="fieldset">
                    
                    <RadioGroup row aria-label="gender" name="gender1" value={values.doctorcare} onChange={handleChangeDoctorcare}>
                        <FormControlLabel value="Yes" control={<Radio />} label="Yes" />
                        <FormControlLabel value="No" control={<Radio />} label="No" />
                    </RadioGroup>
                </FormControl>
                </Grid>
                <Grid>
                <div>
                    {values.doctorcare === 'Yes' ? (
                        <Grid container item xs={12}>
                        <FormLabel container style={{marginTop:-25, lineHeight:2, fontSize:18}}><strong>{t('internalMedcine.currentlyonmedicationyesno')} <br/>/</strong></FormLabel>
                        <GridList style={{ height: 200, padding: 20 }} cellHeight={10} className={classes.gridList} cols={3}>
                        {ondoctorcares.map((value) => {
                            const labelId = `checkbox-list-label-${value.name}`;

                            return (
                                <ListItem key={value.name} role={undefined} dense button onClick={handleToggleDoctorcaresType(value)}>
                                    <ListItemIcon>
                                        <Checkbox
                                            edge="start"
                                            checked={values.doctorCare.indexOf(value.value) !== -1}
                                            tabIndex={-1}
                                            disableRipple
                                            inputProps={{ 'aria-labelledby': labelId }}
                                        />
                                    </ListItemIcon>
                                    <ListItemText id={labelId} primary={`${value.name}`} />

                                </ListItem>
                            );
                        })}
                        </GridList>
                        </Grid>
                    ) : (
                            <nav> &apos; </nav>
                        )}
                </div>
                </Grid>

                <Grid item xs={12}>
                    <Paper className={classes.paper}><strong><h5>{t('internalMedcine.hadsurgery')}? <br />/</h5></strong></Paper>
                </Grid>
                <Grid item xs={12}>
                <FormControl component="fieldset">
                    
                    <RadioGroup row aria-label="gender" name="gender1" value={values.hadsurgery} onChange={handleChangeHadsurgerys}>
                        <FormControlLabel value="Yes" control={<Radio />} label="Yes" />
                        <FormControlLabel value="No" control={<Radio />} label="No" />
                    </RadioGroup>
                </FormControl>
                </Grid>
                <Grid>
                <div>
                    {values.hadsurgery === 'Yes' ? (
                        <div>
                            <FormLabel style={{marginLeft:-148, fontSize:18}}><strong>{t('internalMedcine.currentlyonmedicationyesno')}</strong></FormLabel>
                        <GridList style={{ height: 233, padding: 20 }} cellHeight={10} className={classes.gridList} cols={3}>
                        {hadsurgerys.map((value) => {
                            const labelId = `checkbox-list-label-${value.name}`;
                           return (
                                <ListItem key={value.name} role={undefined} dense button onClick={handleToggleHadsurgerysType(value)}>
                                    <ListItemIcon>
                                        <Checkbox
                                            edge="start"
                                            checked={values.hadsurgerys.indexOf(value.value) !== -1}
                                            tabIndex={-1}
                                            disableRipple
                                            inputProps={{ 'aria-labelledby': labelId }}
                                        />
                                    </ListItemIcon>
                                    <ListItemText id={labelId} primary={`${value.name}`} />

                                </ListItem>
                            );
                        })}
                        </GridList>
                        </div>
                    ) : (
                            <nav> &apos; </nav>
                        )}
                </div>
                </Grid>
            </Grid>
        </div>
    );
}
export default CenteredGrid;