import React, { useEffect, useContext }  from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { useTranslation } from 'react-i18next';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import GridList from '@material-ui/core/GridList';
import { ObstetricContext } from '../ObstetricgynecologyContext';
import UserContext from '../context/UserContext';
import Axios from "axios";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    gridList: {
        width: 1000,
       
        textAlign: 'center',
        margin:10,
        padding:3
      },
    button: {
        marginRight: theme.spacing(1),
    },
    instructions: {
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: 'black',
    },
}));




const CenteredGrid = () => {
    const { t, i18n } = useTranslation();
    const problemtodays = [
        {name: t('obstetricGynecology.Pregnancy') + " /", value:"Pregnancy"},
        {name: t('obstetricGynecology.Menstrualdisorder')+" /", value:"Menstrual disorder"},
        {name: t('obstetricGynecology.Menstrualpain') +" /", value:"Menstrual pain"},
        {name: t('obstetricGynecology.Vaginaldischarge') +" /", value:"Vaginal discharge"},
        {name: t('obstetricGynecology.Abnormalvaginalbleeding')+" /", value:"Abnormal vaginal bleeding"},
        {name: t('obstetricGynecology.Painwhenurinating')+" /", value:"Pain when urinating"},
        {name: t('obstetricGynecology.Difficultyurinating')+" /", value:"Difficulty urinating"},
        {name: t('obstetricGynecology.Hematuria')+" /", value:"Hematuria (blood inurine)"},
        {name: t('obstetricGynecology.Pyuria')+" /", value:"Pyuria (pus in urine)"},
        {name: t('obstetricGynecology.Perineumrash')+" /", value:"Perineum rash"},
        {name: t('obstetricGynecology.Rednessandswelling')+" /", value:"Redness and swelling"},
        {name: t('obstetricGynecology.Havepain')+" /", value:"Have pain"},
        {name: t('obstetricGynecology.Itchiness')+" /", value:"Itchiness"},
        {name: t('obstetricGynecology.Urinaryincontinence')+" /", value:"Urinary incontinence"},
        {name: t('obstetricGynecology.Fecalincontinence')+" /", value:"Fecal incontinence"},
        {name: t('obstetricGynecology.Uterineprolapse')+" /", value:"Uterine prolapse"},
        {name: t('obstetricGynecology.Consultationonfertilitytreatment')+" /", value:"Consultation on fertility treatment"},
        {name: t('obstetricGynecology.Vomiting')+" /", value:"Vomiting"},
        {name: t('obstetricGynecology.Nausea' )+" /", value:"Nausea"},
        {name: t('obstetricGynecology.Cancerscreening')+" /", value:"Cancer screening"},
        {name: t('obstetricGynecology.Other(s)' )+" /", value:"Other(s)"},
        {name: t('obstetricGynecology.advisedbyanother')+" /", value:"I was advised by another clinic/hospital (or at a regular check-up) to come here."},
        
       ]
    

    const [values, setValues] = useContext(ObstetricContext);
    const classes = useStyles();
    const { userData } = useContext(UserContext);
    const history = useHistory();

    useEffect(() => {
        if (!userData.user) history.push("/login1");
    });

    const authAxios = Axios.create({
        baseURL: "http://localhost:5000",
        headers: {
          'x-auth-token': userData.token,
        },
      });

    const logFunction = async (question) =>{
        const loginfo = {
            inteviewName: "Obstetric and Gynecology form 2",
            //userName: userData.user.userName,
            language: i18n.language,
            contentSentence: question,
            date: new Date,
            userId: userData.user.id,
          };
          const loginInput = await authAxios.post(
            "/logfile/insert",
            loginfo
          );
    } 

    const handleToggleProblemtoday = (object) => () => {
        const currentIndex = values.problemtoday.indexOf(object.value);
        const newChecked = [...values.problemtoday];
        
        if (currentIndex === -1) {
            newChecked.push(object.value);
        } else {
            newChecked.splice(currentIndex, 1);
        }
        
         setValues({...values, problemtoday:newChecked});
         logFunction("What is the problem today");
    };

   

    return (

        <div>
            <div className={classes.root}>
                <Grid container spacing={3} style={{ padding: 20 }}>
                    <Grid item xs={12}>
                        <Paper className={classes.paper}><strong><h4>{t('internalMedcine.problemtoday')}<br/>/.)</h4></strong></Paper>
                    </Grid>
                    
                    <Grid container spacing={3} style={{ padding: 20 }}>
                    <GridList container style={{height:642, padding: 20 }} cellHeight={10} className={classes.gridList} cols={3}>
                        {problemtodays.map((value) => {
                            const labelId = `checkbox-list-label-${value.name}`;

                            return (
                                <ListItem style={{marginTop:61}} key={value.name} role={undefined} dense button onClick={handleToggleProblemtoday(value)}>
                                    <ListItemIcon >
                                        <Checkbox
                                            edge="start"
                                            checked={values.problemtoday.indexOf(value.value) !== -1}
                                            tabIndex={-1}
                                            disableRipple
                                            inputProps={{ 'aria-labelledby': labelId }}
                                        />
                                    </ListItemIcon>
                                    <ListItemText id={labelId} primary={`${value.name}`} style={{display:"table-caption"}}/>

                                </ListItem>
                            );
                        })}
                    </GridList>

                    
                        </Grid>
                    </Grid>

            </div>
        </div>

    )
}
export default CenteredGrid;